.Screen{
  min-height: 100vh;
  background-color: #131a2c;

}

.row {
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #131a2c;
  color: white;
  padding: 25px;
}

.col {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #131a2c;
  color: white;
}

.rightCol {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #131a2c;
  color: white;
  margin:50px;
  
}

.ButtonBar{
  display: flex;
  flex-direction: row;
  align-items: right;
  justify-content: right;
  font-size: calc(10px + 2vmin);
  background-color: #131a2c;
  color: white;
}

.ButtonDiv{
  margin: 15px 25px;
}

.icon{
  height:50px;
  width: 50px;
}

.Name{
  font-size: calc(10px + 5vmin);  
  font-weight: bold;
  margin: 20px;
  text-align: left;
  width:700px;
}

.Intro{
  font-size: calc(2px + 2vmin);
  color: #8fa8c6;
  width:700px;
  letter-spacing: 1px;
  font-weight: 600px;
  text-align: left;
}

.Template{
  padding-top:2vh;
  padding-bottom:2vh;
}

.Title{
  font-weight:bold;
}

.CompDate{
  /* color: #473BF0; */
  color: #C17C74;
  font-size: calc(2px + 1.5vmin);
}

.Points{
  padding:20px;
  font-size: calc(4px + 1.5vmin);
  width: 500px;
 align-items: center;
  justify-content: center;
}

.NavBar{
  text-align: left;
  width:600px;
  margin: 20px;
  font-size: calc(2px + 2vmin);
  color:#FFFFFF

}

.Scroll{
  width:500px;
  font-size: calc(2px + 2vmin);
  text-align: left;
  margin: 10px;
}

.ScrollItem{
  width:500px;
  font-size: calc(2px + 2vmin);
  text-align: left;
  padding:50px;
}


.Nav:hover{
  /* font-size: calc(2px + 2vmin); */
  color: #8FA8C6;
}